.clinic-page {
	&__title {
		margin-bottom: 19px; }
	&__title-sec {
		margin-bottom: 15px;
		@include md {
			text-align: center;
			& + p {
				text-align: center; } } }
	&__white-block {
		background: $white;
		padding-top: 50px;
		padding-bottom: 50px;
		&--pb-0 {
			padding-bottom: 0; } }
	&__mb-35 {
		margin-bottom: 35px; }
	&__mb-27 {
		margin-bottom: 27px; }
	&__mb-50 {
		margin-bottom: 50px; }
	&__mt-30 {
		margin-top: 30px; }
	&__mb-95 {
		margin-bottom: 95px; }
	&__p-50 {
		padding-top: 50px;
		padding-bottom: 50px; }
	&__branches {
		.clinic-box {
			margin-bottom: 20px;
			&:last-of-type {
				margin-bottom: 0; } }
		.btn--green {
			min-width: 175px; }
		.more-link {
			margin-top: 10px; } }
	.white-block {
		background: $white; }
	.costs {
		margin-bottom: 19px;
		@include md {
			position: relative;
			left: -40px;
			width: calc(100% + 80px); } }
	.filters {
		margin-bottom: 20px; }
	.clinic-gallery {
		margin-bottom: 95px; }
	.services {
		padding-bottom: 50px; }
	.clinic-boxes {
		padding-top: 50px; } }
