.doctors-page {
	&__title {}
	&__popular-links {
		margin-top: 28px;
		margin-bottom: 25px;
		@include md {
			display: none; }
		h4 {
			margin-bottom: 10px; } }
	&__map {
		@include md {
			display: none; }
		.map-link {
			height: 145px; } }
	&__mb-27 {
		margin-bottom: 27px;
		@include clr; }
	&__white {
		background: $white;
		padding-top: 100px; }
	&__mb-45 {
		margin-bottom: 45px; }
	&__list {
		margin-bottom: 30px; }
	.filter-section {
		margin-bottom: 20px; }
	.more-link {
		&__icon {
			top: 8px; } }
	.top-info__map {
		padding-top: 30px;
		.map-link {
			margin: 0 auto 5px;
			display: block; } } }
