.clinics {
	&__map {
		@include md {
			display: none; } }
	&__topmap {
		.map-link {
			height: 88px;
			p {
				padding: 4px 5px 1px; } } }
	&__filters {
		margin-bottom: 28px;
		// +md
 }		// 	display: none
	&__popular-links {
		.h4 {
			margin-bottom: 10px; }
		@include md {
			display: none; } }
	&__sales {
		padding: 100px 0 50px; }
	&__services {
		.services {
			padding-top: 0; } }
	&__doctor-list {
		margin-bottom: 100px; } }
.filter-section {
	&__title {
		margin-bottom: 16px;
		@include md {
			display: none; } }
	&__taglist {
		margin-bottom: 8px;
		@include md {
			display: none; } }
	&__meta-filters {
		margin-bottom: 16px;
		@include md {
			display: none; } }
	&__content {
		margin-bottom: 50px;
		.top-info__map {
			padding-top: 30px;
			.map-link {
				margin: 0 auto 50px;
				display: block; } } }
	&__more {
		text-align: center;
		.button {
			background-color: #fff;
			&:hover {
				background-color: $cyan; } } }
	.top-info__map {
		.map-link {
			margin: 0 auto 50px; } } }

