.doctor {
	display: flex;
	&__left {
		margin-right: 30px;
		.btn {
			width: 175px; }
		@include r(720) {
			display: none;
			margin-right: 0; } }
	&__right {
		flex-grow: 1; }
	&__right-rateline {
		display: none;
		@include r(720) {
			display: flex;
			justify-content: space-between;
			margin-bottom: 10px; } }
	&__right-topline {
		@include clr;
		@include r(720) {
			width: 100%;
			padding-bottom: 10px;
			margin-bottom: 10px;
			border-bottom: 1px solid $gray-border; } }
	&__avatar {
		margin-bottom: 20px;
		height: 220px;
		width: 175px;
		overflow: hidden;
		border-radius: 5px;
		border: 1px solid $gray-border;
		@include r(720) {
			display: none; }
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; }
		&--mob {
			display: none;
			height: 109px;
			width: 78px;
			margin-right: 10px;
			margin-bottom: 0;
			@include r(720) {
				display: inline-block;
				float: left; } } }
	&__lists {
		@include r(720) {
			border-bottom: 1px solid $gray-border;
			margin-bottom: 10px; } }
	&__list {
		font-size: 14px;
		line-height: 24px;
		@include r(720) {
			font-size: 12px; }
		span {
			@include OSBold; } }
	&__title {
		font-size: 15px;
		color: #cdd0d1;
		margin-bottom: 8px;
		strong {
			color: $black;
			display: inline;
			@include r(720) {
				display: block; } } }
	&__experience {
		font-size: 15px;
		@include PBold;
		color: #cdd0d1;
		margin-bottom: 14px; }
	&__lists {
		h6 {
			font-size: 14px;
			@include OSBold; }
		.list {
			margin: 8px 0 16px; } }

	.topline {
		display: flex;
		justify-content: space-between;
		&__left {
			@include r(720) {
				display: none; }
			&--left {
				display: none;
				@include r(720) {
					display: block; } } } } }
