.menu {
	ul {
		li {
			display: inline-block;
			vertical-align: bottom;
			position: relative;
			&:not(:last-child) {
				margin-right: 17px;
				@include lg {
					margin-right: 8px; } }
			a {
				font-size: 15px;
				@include PBold;
				@include lg {
					font-size: 13px; } }
			i {
				position: absolute;
				top: -1px;
				right: -10px;
				font-size: 9px;
				@include PBold;
				color: #f3e7ec;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 50%;
				min-height: 15px;
				min-width: 15px;
				background-color: $pink;
				line-height: 1;
				z-index: -1;
				font-style: normal;
				@include lg {
					min-width: 10px;
					min-height: 10px;
					font-size: 8px;
					right: -5px;
					top: 2px; } } } } }
.burger {
	position: relative;
	height: 17px;
	width: 22px;
	background-color: transparent;
	&:before, &:after, i {
		width: 100%;
		position: absolute;
		left: 0;
		height: 3px;
		background-color: #1a1a1a;
		transition: .25s ease;
		border-radius: 50px;
		@include md {
			background-color: $cyan; } }
	&:before, &:after {
		content: '';
		transform: rotate(0) scaleX(1); }
	&:before {
		top: 0;
		transform-origin: top right; }
	i {
		top: 7px;
		transform: scale(1);
		opacity: 1; }
	&:after {
		bottom: 0;
		transform-origin: bottom right; }
	&.is-active {
		&:before {
			transform: rotate(-39deg) scaleX(1.05); }
		&:after {
			transform: rotate(39deg) scaleX(1.05); }
		i {
			transform: scale(.2);
			opacity: 0; } } }


.auth {
	display: inline-block;
	&__item {
		display: inline-block;
		vertical-align: top; }
	&__login {
		margin-right: 16px;
		padding-top: 9px;
		cursor: pointer;
		@include md {
			padding-top: 11px;
			margin-right: 0; } }
	&__reg {
		@include lg {
			margin-top: 3px; }
		@include md {
			display: none; } } }

.login {
	&__icon {
		display: inline-block;
		vertical-align: top;
		.icon {
			fill: #29c1e2;
			font-size: 18px;
			@include lg {
				font-size: 15px; }
			@include md {
				font-size: 19px; } } }
	&__text {
		display: inline-block;
		vertical-align: top;
		padding-top: 5px;
		@include lg {
			font-size: 13px; }
		@include md {
			display: none; } }
	&__text {
		font-size: 15px;
		@include PBold; } }

